.calendar {
  &__body{
    padding: 5px;
    background: var(--ui-white);
    border-radius: 37px;
  }

  &__items {
    display: flex;
    align-items: center;
    overflow: auto;

    &::-webkit-scrollbar {
      width: 100%;
      height: 4px;
      background: #d0d0d0;
      border-radius: 37px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: #2c2f31;
      border-radius: 37px;
    }
  }

  &__item {
    //width: 10%;
    border-radius: 37px;
    min-width: 140px;
    flex-grow: 1;
    @include max400 {
      min-width: 100px;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.4;
    text-align: center;
    color: var(--typography-black);
    transition: 0.3s;
    border-radius: 37px;
    padding: 4px 10px;
    span{
      color: var(--typography-black-medium-contrast);
    }

    @media screen and (min-width: 1024px) {
      &:hover{
        background: var(--ui-accent, #FFB21C);
      }
    }

    @include max400 {
      font-size: 14px;
    }
  }
}
.calendar--mb {
  margin-bottom: 60px;

  @include max600 {
    margin-bottom: 40px;
  }
}
